import React, {Component} from 'react';
import partnerlogo from '../../img/logo-christian.png'
import './style.css'
class Partner extends Component {
    render() {
        return(

            <div className="partnercentralize">
            <img src={partnerlogo}
            width='300'
            alt="partner"
            height='300'/>
            </div>
        )
    }
}

export default Partner;