import React, {Component} from 'react';
import {Col, Container, Row} from 'react-bootstrap';
import './style.css'
import App from '../modal/modal'
import Projeto2 from '../../img/chris-bus-logo.png'
import Projeto3 from '../../img/katy-malhas-logo.png'


class Home extends Component {
    
    render() {
        return(

            <div className="homecss puff-in-center">
                <Container >
                    <Row noGutters>
                        <Col className="zoom " md={4}  >
                            <App/>
    
                        </Col>
                        <Col className="zoom" md={4}>
                            <img src={Projeto2}
                            alt="projeto2"
                            height="300"
                            width="100%"
                            />
                        </Col>
                        <Col className="zoom" md={4}>
                            <img src={Projeto3}
                            alt="projeto3"
                            height="300"
                            width="100%"
                          />
                        </Col>

                    </Row>

                </Container>
            </div>
        )
    }
}

export default Home;