import React, {Component} from 'react';
import {Form, Col, Container} from 'react-bootstrap';
import './style.css'

class Contact extends Component {
    render() {
        return(
        <Form className="slide-in-fwd-left">
            <Container style={{width:'30%'}}>
            <Col md={12}>
                <h1 align="center">Entre em contato</h1>
        <Form.Group className="borderperson" controlId="exampleForm.ControlInput1">
            <Form.Control className="stylize" type="text" placeholder="NOME" />
        </Form.Group>

        <Form.Group className="borderperson" controlId="exampleForm.ControlInput1">
            <Form.Control className="stylize" type="email" placeholder="EMAIL" />
        </Form.Group>


        <Form.Group  className="borderperson" controlId="exampleForm.ControlTextarea1">
            <Form.Control className="stylize" as="textarea" rows="3" placeholder="MENSAGEM" />
        </Form.Group>
        </Col>
        </Container>
  
        </Form>

        )
    }
}

export default Contact;