import React from 'react';
import {Navbar, Nav, Container, Col, Row} from 'react-bootstrap'
import Logo from '../../img/kv-design-logo.png'
import Facebook from '../../img/kv-social-facebook.png'
import Instagram from '../../img/kv-social-instagram.png'
import {Link} from 'react-router-dom'
import Roteamento from '../../routes'
import './style.css'

function navbar (){
  return (
    <>
    <div className="App">
        <Navbar collapseOnSelect expand="sm">
            <Navbar.Brand><img src={Logo} alt="logo" width='180' height='180'></img></Navbar.Brand>
            <Navbar.Toggle aria-controls="responsive-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
                <Nav className="ml-auto blacknav">

                    <Link className="links" to='/home'>Home</Link> 
                    <Link className="links" to='/about'>Sobre</Link>
                    <Link className="links"to="/contact">Contato</Link>
                    <Link className="links"to="/partners">Parceiros</Link>


                </Nav>
            </Navbar.Collapse>
      
        </Navbar>
    
        </div>
                <br></br>
                <br></br>
                <br></br>
             <Roteamento/>

        <div className="Footer">
        <br></br>
                    <br></br>
                    <br></br>
                    <br></br>
                    <br></br>
                    <br></br>
                    <br></br>
                    <br></br>
                    <br></br>
                    <br></br>


                 <Container>
                     <Row>
                     <Col md={6} lg={6}>
                    <h5>41 996539478</h5>
                    <h5>kariiiinevasco@gmail.com</h5>
                    </Col>

                    <Col md={6} lg={6} className="socialmedia">
                    <img src={Facebook} alt="facebook"></img>
                    <img src={Instagram} alt="instagram"></img>
                    </Col>
                        </Row>
                        <Row style={{display:'flex', justifyContent:'center'}}>
                            <h5>Desenvolvido por Christian Alves</h5>
                        </Row>
                    </Container>
         

        </div>
             </>

  );
}



export default navbar;
