import React from 'react'
import {Modal} from 'react-bootstrap'
import Logo from '../../img/kv-design-logosimples.png'
import './style.css'
import Projeto1 from '../../img/christianalves-logo.png'
function MyVerticallyCenteredModal(props) {
    return (
      <Modal
        {...props}
        size="md"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton className="banner">
          <Modal.Title id="contained-modal-title-vcenter">
            <img src={Logo}
            width='70'
            alt="logo"
            height='70'/>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body style={{display:'flex', justifyContent:'center'}} className="teste">
          <img src={Projeto1}
                    height="300"
                    alt="projeto1"
                    style={{borderRadius:'10px'}}
                    width="70%"/>
        </Modal.Body>
        <Modal.Footer className="footer">
        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam viverra malesuada laoreet. Cras scelerisque diam eget iaculis semper. Interdum et malesuada fames ac ante ipsum primis in faucibus.
            </p>
        </Modal.Footer>
      </Modal>
    );
  }
  
  function App() {
    const [modalShow, setModalShow] = React.useState(false);
  
    return (
            <>
                <img src={Projeto1}
                    height="300"
                    width="100%"
                    alt="projeto11"
                    onClick={() => setModalShow(true)}/>

            
                    <MyVerticallyCenteredModal
                    show={modalShow}
                    onHide={() => setModalShow(false)}
                    />
            </>

    );
  }
  
export default App;